.movie__list__container {
  max-width: calc(100vw - 48px);
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  grid-gap: 14px;
  padding: 0;
  margin-top: 25px;
  padding: 0;
  margin-left: 25px;
  margin-right: 25px;
  padding-left: 5px;
  padding-right: 5px;
}
